
.login{
	margin: 0 auto;
	text-align: center;
	width: 350px;
	height: 300px;
	background: rgba(0,0,0,0.2);
}
#main_loginAnimate{
	min-height: 700px;
	text-align: center;
	background: url("../../assets/img/picture/unlimitedBlade3.jpg") no-repeat;
	background-size:100% ;
}
#login_filled{
	width: 100%;
	height: 10%;
	border: 1px solid #000;
}
#login_form{
	margin-top: 10%;
}
#login_form input{
	text-align: center;
	width: 200px;
	height: 40px;
	margin-top: 40px;
	background: transparent !important;
	border: 1px solid #000;
	color: #e98;
}
#login_form input:nth-child(3){
	cursor: pointer;
	color: #e98;
}



